.doc-page {
    &-index {
        width: 50%;
        &__header {
            display: inline-block;
            color: $docpageheadercolor;
            margin-top: 2rem;
            font-size: 200%;
            // text-transform: uppercase;
            &-smallcaps {
                font-variant:  small-caps;
            }

            &__version {
                text-transform: lowercase;
            }
        }

        &__list-container {
            height: 100%;
            width: 100%;
        }

        &__list {
            list-style: none;

            &-item {
                padding-left: 0.5rem;

                &-link {
                    display: block;
                    width: 100%;
                    font-size: 0.9rem;
                    color: $linkcolor;
                    margin-bottom: 1px;
                    padding: 0 0.333rem;
                    border: 1px solid transparent;
                    text-decoration: underline;
                }
            }

            // &-item:before {
            //     content: "▪";
            //     float: left;
            //     margin-right: 0.5rem;
            //     color: $linkcolor;
            //     line-height: 100%;
            // }

            &-item-link:hover,
            &-item-link:focus {
                background-color: rgba($linkcolor, 0.1);
                color: $linkcolor;
                border-radius: 3px;
                font-weight: bold;
                border-color: $linkcolor;
            }

            &-item-link--current {
                background-color: rgba($linkcolor, 0.1);
                border-radius: 3px;
                color: $linkcolor;
                font-weight: bold;
            }
        }
    }

    &-content {
        margin-top: 2rem;
        width: 100%;

        &__copy {
            margin-top: 3rem;
            margin-bottom: 3rem;

            blockquote {
                font-style: italic;
                margin: 1rem;
                color: $highlightcolor;
                pre {
                    margin-top: 1rem;
                }
            }

            h1,
            h2,
            h3,
            h4,
            h5,
            h6 {
                color: $docpageheadercolor;
            }

            p + p,
            ul + p,
            ol + p,
            p + pre,
            p + figure,
            ul + figure,
            ol + figure,
            figure + figure,
            pre + p,
            p + ul,
            p + ol {
                margin-top: 1rem;
            }

            li,
            li p {
                margin-top: 0.5rem;
            }

            h3 + p,
            h4 + p,
            h5 + p,
            h6 + p,
            pre + h2,
            pre + h3,
            pre + h4,
            pre + h5,
            pre + h6,
            h2 + pre,
            h3 + pre,
            h4 + pre,
            h5 + pre,
            h6 + pre,
            h2 + h3,
            h3 + h4,
            h4 + h5,
            h5 + h6 {
                margin-top: 1.5rem;
            }

            figure + ul,
            figure + ol,
            figure + h2,
            figure + h3,
            figure + h4,
            figure + h5,
            figure + h6,
            figure + p,
            pre + p,
            ul + h2,
            ol + h2,
            p + h2,
            p + h3 {
                margin-top: 3rem;
            }

            ul + h3,
            ul + h4,
            ul + h5,
            ul + h6,
            ol + h3,
            ol + h4,
            ol + h5,
            ol + h6,
            p + h4,
            p + h5,
            p + h6 {
                margin-top: 2rem;
            }

            h1 {
                font-size: 2.5rem;
            }

            h2 {
                font-size: 1.25rem;
                border-top: 1px solid rgba($color: #000000, $alpha: 0.25);
                margin-top: 1.5rem;
                margin-bottom: 1.5rem;
                padding-top: 3rem;
            }

            h3,
            h4,
            h5,
            h6 {
                font-size: 1.1rem;
                margin-bottom: 0;
            }

            em {
                font-style: normal;
                font-weight: bold;
                background-color: rgba($secondary-text-color, 0.08);
                padding: 0 0.5rem;
            }

            li a,
            p a {
                text-decoration: none;
                background-color: rgba($secondary-text-color, 0.08);
                color: $normalcolor;
                border-bottom: 1px solid rgba($secondary-text-color, 0.3);
                font-weight: normal;
                &:hover {
                    background-color: rgba($secondary-text-color, 0.15);
                    border-bottom: 1px solid rgba($secondary-text-color, 0.9);
                    font-weight: 600;
                }
            }

            li,
            p {
                color: $normalcolor;

                mark {
                    padding: 0 0.5rem;
                    color: $docpageheadercolor;
                    background-color: rgba(yellow, 0.1);
                    font-weight: bold;
                }

                strong {
                    background-color: rgba($docpageheadercolor, 0.7);
                    color: white;
                    font-weight: bold;
                    padding: 0 0.5rem;
                    margin-right: 0.1rem;
                    border-radius: 3px;
                }
            }

            ul {
                list-style: none;
            }

            ul > ul,
            ol > ol {
                margin-left: 1rem;
            }

            ol {
                margin-left: 1rem;
            }

            ul li:before {
                content: "▪";
                float: left;
                margin-right: 0.5rem;
                color: $darkest-background-color;
                font-size: 1.3rem;
            }

            &-header {
                color: $docpageheadercolor;
            }

            figure {
                display: block;

                img {
                    display: block;
                    width: 100%;
                    height: auto;
                    border-top-left-radius: 3px;
                    border-top-right-radius: 3px;
                }

                figcaption {
                    text-align: center;
                    background-color: rgba($secondary-text-color, 0.45);
                    color: #000;
                    font-weight: bold;
                    border-bottom-left-radius: 3px;
                    border-bottom-right-radius: 3px;
                    box-shadow: 0 2px 3px rgba($secondary-text-color, 0.2);
                    padding: 0.5rem;
                    letter-spacing: 1px;
                }
            }

            pre {
                display: block;
                white-space: pre;
                margin: 0;
                border: 1px solid rgba(#000, 0.1);

                code {
                    padding: 1rem;
                    background-color: #fff;
                }
            }

            %note {
                display: block;
                width: 100%;
                border-left: 10px solid rgba($secondary-text-color, 0.35);
                border-radius: 3px;
                margin: 2rem 0;
                padding: 20px;
                background-color: rgba(yellow, 0.1);
                font-style: normal;
            }

            .note-important {
                @extend %note;
                &:before {
                    content: "Important";
                    display: block;
                    color: darkorange;
                    font-weight: bold;
                    font-size: 2rem;
                    margin-bottom: 1rem;
                }
            }
            .note-tip {
                @extend %note;
                &:before {
                    content: "Tip";
                    display: block;
                    color: black;
                    font-weight: bold;
                    font-size: 1.1rem;
                    margin-bottom: 0.5rem;
                }
            }
        }
    }
}

@include media-query-up(lg) {
    .doc-page {
        display: flex;

        &-index {
            &__header {
                margin-top: 0;
            }
            position: fixed;
            overflow-y: auto;
            z-index: 9999;
            margin-top: 2rem;
            width: 250px;
            padding-right: 10px;
            max-height: 100%;
            padding-bottom: 10rem;

            &__list-container {
                padding-right: 10px;
            }
        }

        &-content {
            flex: 0 0 auto;
            margin-left: 300px;
            max-width: calc(75% - 4rem);

            &__copy {
                margin-top: 0;

                &-under-construction {
                    font-size: 3.5rem;
                }
            }
        }
    }
}
