  border: 1px solid rgba(0, 100, 0, 0.35);
  border-radius: 0.25rem;
  box-shadow: 3px 3px 20px 5px rgba(144, 238, 144, 0.55);
  margin: 0 auto 3rem auto;
  padding: 0.333rem;
  text-align: center;
}

.markdown-body {
  display: block;
}

.markdown-body img {
  width: 100%;
  padding: 5px;
  border-radius: 0.25rem;
  border: solid 1px #cecece;
